<template>
    <div>
        <van-nav-bar title="待报修处理"/>
        <van-cell-group v-for="(item,index) in wait_list" :key="index">
            <van-cell v-if="item.frepair_state==='内修'" :value="item.repair_date" :label="item.frepair_type" is-link :to="{path:'/insiderepair',query:{fid:item.fid}}">
                <template #title>
                    <span class="custom-title">{{item.type}}报修</span>
                    <van-tag type="primary">{{item.frepair_state}}</van-tag>
                </template>
            </van-cell>
            <van-cell v-else :value="item.repair_date" :label="item.frepair_type" is-link :to="{path:'/outsiderepair',query:{fid:item.fid}}">
                <template #title>
                    <span class="custom-title">{{item.type}}报修</span>
                    <van-tag type="warning">{{item.frepair_state}}</van-tag>
                </template>
            </van-cell> 
        </van-cell-group>     
    </div>
</template>

<script>
import {apiwaitlist} from "@/api/index"
export default {
    name:'waitRepair',
    data() {
        return {
           wait_list:[],
           repair_show:false,
        }
    },
    methods: {
        getlist(){
            apiwaitlist().then(res=>{
                console.log(res.data)
                this.wait_list=res.data.data
                
            })
        }
    },
    created(){
       this.getlist();
    }
}
</script>

<style>

</style>


